import {
  Injectable
} from '@angular/core';
import {
  ActivatedRoute,
  Router
} from '@angular/router';
import {
  OAuthService,
  OAuthStorage
} from 'angular-oauth2-oidc-codeflow';
import {
  JwksValidationHandler
} from 'angular-oauth2-oidc-codeflow';
import {
  authConfig
} from './auth.config';
import {
  Configuration
} from '../api-client';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private _configurePromise: Promise<any>;

  constructor(
    private oauthService: OAuthService,
    private oauthStorage: OAuthStorage,
    private route: ActivatedRoute,
    private router: Router,
    private apiConfiguration: Configuration) {

  }

  public configure(): Promise<void> {

    // If there is a configure in process just return that
    if (this._configurePromise) {
      return this._configurePromise;
    }

    this.oauthService.configure(authConfig);
    this.oauthService.tokenValidationHandler = new JwksValidationHandler();

    return this._configurePromise = this.oauthService.loadDiscoveryDocument()
      .then(() => {

        // 20190312RBP - Added state checking because angular-oauth2-oidc-codeflow does not seem to do it
        // see https://tools.ietf.org/html/rfc6749#section-10.12
        if (this.route.snapshot.queryParamMap.has('state')) {
          const state = this.route.snapshot.queryParamMap.get('state');
          if (!this.validateState(state)) {
            throw new Error('State is invalid, cannot login');
          }
        }

        return this.oauthService.tryLogin();
      })
      .then(() => {
        console.log('Logged in');

        const returnUrl = this.oauthStorage.getItem('__cocReturnUrl');

        // Set authorization for API client
        this.setApiAuthorization(this.oauthService.getAccessToken());

        if (returnUrl) {
          // login successful so redirect to return url
          this.oauthStorage.removeItem('__cocReturnUrl');
          this.router.navigateByUrl(returnUrl);
        }

      })
      .catch(err => {
        console.log('Unable to login');
      });

  }

  public async isSignedIn(): Promise<boolean> {

    await this.ensureConfigured();
    return this.oauthService.hasValidAccessToken();

  }

  public logout(): void {

    // Remove authorization from API client
    this.setApiAuthorization(null);

    this.oauthService.logOut();
  }

  public signIn(returnUrl: string): void {

    // Save the returnUrl to go back to after returning
    // from auth code grant
    this.oauthStorage.setItem('__cocReturnUrl', returnUrl);
    this.oauthService.initAuthorizationCodeFlow();
  }

  private ensureConfigured(): Promise<void> {

    if (!this._configurePromise) {
      return this.configure();
    }

    return this._configurePromise;
  }

  private setApiAuthorization(token: string): void {

    // Set authorization for API client
    // see https://github.com/swagger-api/swagger-codegen/issues/3221#issuecomment-314757762

    if (!this.apiConfiguration.apiKeys) {
      this.apiConfiguration.apiKeys = {};
    }

    if (token === null) {
      delete this.apiConfiguration.apiKeys['Authorization'];
    } else {
      this.apiConfiguration.apiKeys['Authorization'] = `Bearer ${token}`;
    }

  }

  private validateState(state: string): boolean {

    const savedState = this.oauthStorage.getItem('nonce');

    if (savedState !== state) {
      const err = 'getting access_token failed. wrong state/nonce.';
      console.error(err, savedState, state);
      return false;
    }

    return true;
  }
}
